import Vue from 'vue'
import VueRouter from 'vue-router'

Vue.use(VueRouter)

const routes = [

    {
        path: '/',
        redirect:'/Home'    //重定向
    },
    {
        path: '/Home',
        name: 'Home',
        component: () => import( '@/views/Home'),
    },
]

const router = new VueRouter({
    routes,
    scrollBehavior(to,from,savedPosition){
        console.log(to,from,savedPosition);
        if(savedPosition){
            return savedPosition;
        }else{
            return {x:0,y:0}
        }
    }
  })

export default router;